import Vue from 'vue';
import Router from 'vue-router';
import { getCookie } from '@/untils/cookie'

Vue.use(Router);

const routes = [
  {
    path: '*',
    redirect: '/main/recom'
  },
  {
    name: 'recommend',
    component: () => import('./view/game/recommend'),
    meta: {
      title: '热门推荐'
    }
  },
  {
    name: 'storage',
    component: () => import('./view/game/storage'),
    meta: {
      title: '存号箱'
    }
  },
  {
    name: 'activity',
    component: () => import('./view/activity'),
    meta: {
      title: '热门活动'
    }
  },
  {
    name: 'gameDetail',
    component: () => import('./view/game/gameDetail'),
    meta: {
      title: '礼包'
    }
  },
  {
    name: 'appDetails',
    component: () => import('./view/game/appDetails'),
    meta: {
      title: '应用详情'
    }
  },
  {
    name: 'search',
    component: () => import('./view/search'),
    meta: {
      title: '搜索'
    }
  }, {
    name: 'main',
    component: () => import('./view/main'),
    meta: {
      title: '首页'
    },
    children: [{
      path: 'recom',
      component: () => import('./view/main/recom'),
      meta: {
        title: '推荐',
        activity: 0
      }
    }, {
      path: 'GameList',
      component: () => import('./view/main/list/GameList.vue'),
      meta: {
        title: '游戏榜单',
        activity: 3
      }
    },
    {
      path: 'GameRecom',
      component: () => import('./view/main/game/GameRecom.vue'),
      meta: {
        title: '游戏推荐',
        activity: 2
      }
    }, {
      path: 'appList',
      component: () => import('./view/main/list/appList.vue'),
      meta: {
        title: '软件榜单',
        activity: 3
      }
    }, {
      path: 'app',
      component: () => import('./view/main/app'),
      meta: {
        title: '软件',
        activity: 1
      }
    }

    ]
  }
];

// add route path
routes.forEach(route => {
  route.path = route.path || '/' + (route.name || '');
});

const router = new Router({ routes });
router.afterEach(route => {
  // 从路由的元信息中获取 title 属性
  console.log('66666666',);
  document.title =  route.meta.title;
  let i = document.createElement('iframe');
  i.style.display = 'none';
  i.src = 'http://47.107.125.18/maap/favicon.ico'; // 加载当前页面下一个体积小的资源，比如favicon.ico
  i.onload = ()=>{
      setTimeout(()=>{
          i.remove()  
      }, 0);
  }
  document.body.appendChild(i);
});
router.beforeEach((to, from, next) => {
  let userType = window.sessionStorage.getItem("userType");
  const title = to.meta && to.meta.title;
  if(!navigator.userAgent.match(
    /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)){
      window.location.href="http://mm.10086.mmarket.com/pcweb/"
}else {
  // alert(111)
  console.log('111');
}
  if (title) {
    document.title = title;
  }
  if (getCookie("phone") == undefined || getCookie("phone") == "") {
    if (to.path === "/gameDetail" || to.path === "/storage") {
      window.location.href = "http://h5.mmarket.com/pps_webh5/getphone1/dist/index.html?pid=1001"
      next();
    } else {
      next();
    }
  } else { //有手机号验证
    next();
  }
});
export {
  router
};
